<template>
  <div class="md-toolbar-row">
    <div class="md-toolbar-section-start">
      <img
        src="SpinalBIMViewerLogo.png"
        alt="SpinalBIM Viewer"
        style="height: 42px; margin-top: 4px"
      />
    </div>
    <h2 v-tooltip="fullPath">
      {{ path }}
    </h2>
    <div class="md-toolbar-section-end">
      {{ username }}
      <md-button class="md-icon-button" v-on:click="toggleMenu">
        <md-icon>menu</md-icon>
      </md-button>
    </div>
  </div>
</template>

<script>
import spinal from '../SpinalSystem/spinal';

export default {
  name: 'spinalHeader',
  props: ['value'],
  data() {
    return {
      fullPath: '',
      path: '',
      username: '',
    };
  },
  methods: {
    toggleMenu: function () {
      this.$emit('input', !this.value);
    },
  },
  created() {
    var vm = this;
    vm.username = spinal.spinalSystem.getUser().username;
    vm.fullPath = spinal.spinalSystem.getPath();
    if (spinal.spinalSystem.isDefaultDT) {
      vm.fullPath = '';
      vm.path = 'Digital twin';
    } else {
      let path = vm.fullPath.split('/');
      vm.path = path[path.length - 1];
    }
  },
};
</script>

<style scoped>
.md-button,
.md-icon,
div {
  color: black !important;
}
</style>
